$VERSION: 0.1;
@use 'common/variables-sass' as *;

.friendsui-container {
  @import 'common/shadowEffects/improvedShadow';

  /* ======== Looks better without box-shadow ======= */

  /* Between logged in and quickaccess */
  .activeTab,
  .DialogButton::before,
  .DialogButton,
  .DialogSlider_Slider,
  [class*='radio_Group'],
  .avatar,
  [class*='miniprofile_playerAvatar'],
  .ChatRoomGroupAvatar,
  .ChatMessageEntryVoice,
  [class*='chatentry_chatEntryControls'],
  .chatTabOpenFriendsList,
  .chatTabList .chatTab,
  .chatTabSelector,
  .friendSearchInput {
    box-shadow: none !important;
  }

  /* End of friendlist shadow */
  .ChatRoomListContainerParent [class*='insetshadow_FriendsListInsetShadowCtn'],
  .insetShadow {
    display: none !important;
  }

  /* ================================================ */

  [class*='chatentry_chatTextarea'] {
    border-radius: $border-radius;
    margin-right: 0 !important;
  }

  /* chat invite message */
  .ChatMessageInvite {
    box-shadow: 0 0 6px rgb(0 0 0 / 10%) !important;
    margin: 4px;
  }

  /* Shadows */
  .ModalDialogPopup .DialogContentTransition .DialogBody {
    mask: none !important;
  }

  .DialogTextInputBase:focus {
    box-shadow: none;
  }

  /* Chat text input box */
  .chatWindow .chatEntry {
    box-shadow: 0 0 8px rgb(0 0 0 / 30%);
  }
}
